	module.exports = {"data":{"projectsJson":{"name":"Denoising Deep Monte Carlo Renderings","abstract":"We present a novel algorithm to denoise deep Monte Carlo renderings, in which pixels contain multiple color values, each for a different range of depths. Deep images are a more expressive representation of the scene than conventional flat images. However, since each depth bin receives only a fraction of the flat pixel's samples, denoising the bins is harder due to the less accurate mean and variance estimates. Furthermore, deep images lack a regular structure in depth---the number of depth bins and their depth ranges vary across pixels. This prevents a straightforward application of patch-based distance metrics frequently used to improve the robustness of existing denoising filters. We address these constraints by combining a flat image-space Non-Local Means filter operating on pixel colors with a deep cross-bilateral filter operating on auxiliary features (albedo, normal, etc.). Our approach significantly reduces noise in deep images while preserving their structure. To our best knowledge, our algorithm is the first to enable efficient deep-compositing workflows with denoised Monte Carlo renderings. We demonstrate the performance of our filter on a range of scenes highlighting the challenges and advantages of denoising deep images.","authors":[{"name":"Delio Vicini"},{"name":"David Adler"},{"name":"Jan Novák"},{"name":"Fabrice Rousselle"},{"name":"Brent Burley"}],"published":"Computer Graphics Forum","year":"2018","url":"denoising-deep-monte-carlo","slides":null,"teaser":"teaser.png","teasercaption":"A production scene (a) rendered as a deep noisy image (b), denoised using our method (c), and composited (d) with a deep volume. Our algorithm removes a significant amount of the input Monte Carlo noise, while preserving the depth separation of the deep image. (c) Disney","pdf":"DeepZDenoising.pdf"}},"pathContext":{"slug":"denoising-deep-monte-carlo"}}

